.detailPage {
    width: 100%;
    padding: 0;
    margin: 0;
    font-family: 'Pretendard', 'Noto Sans KR', sans-serif;
    animation: fadeInUp 0.4s ease forwards;
    background-color: #f8f8f8;
    box-sizing: border-box;
}

.detailContainer {
    width: 100%;
    height: 100%;
    margin: 0;
    background-color: #fff;
    padding: 2rem;
    box-sizing: border-box;
}

.detailHeader {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 1.5rem;
}

.detailHeader button {
    background: none;
    border: none;
    font-size: 1.2rem;
    color: #00753C;
    cursor: pointer;
    padding: 0;
    font-weight: bold;
}

.detailHeader button:hover {
    color: #004225;
}

.detailTitle {
    font-size: 2rem;
    font-weight: 700;
    color: #222;
    border-bottom: 2px solid #00753C;
    padding-bottom: 0.4rem;
    margin: 1rem 0;
}

/* ì´ë¯¸ì§€ í¬ê¸° ì¡°ì ˆ */
.detailImage {
    width: 100%;          /* ë¶€ëª¨ ì»¨í…Œì´ë„ˆ ë„ˆë¹„ì— ë§žê²Œ ê½‰ ì±„ìš°ê¸° */
    max-width: 720px;     /* ìµœëŒ€ ë„ˆë¹„ ì œí•œ */
    height: auto;         /* ë¹„ìœ¨ ìœ ì§€ */
    object-fit: contain;  /* ì´ë¯¸ì§€ ë¹„ìœ¨ ìœ ì§€í•˜ë©° ë°•ìŠ¤ ë‚´ì— ë§žì¶¤ */
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-bottom: 1.5rem;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.detailInfo {
    font-size: 1.1rem;
    color: #444;
    margin-bottom: 0.8rem;
}

.detailReview {
    font-size: 1rem;
    color: #666;
    line-height: 1.6;
    margin-top: 1rem;
}

/* Animation */
@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(12px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.buttonGroup {
    margin-top: 1.5rem;
    display: flex;
    gap: 1rem;
    flex-wrap: nowrap;
}

.wishlistBtn {
    padding: 0.6rem 1rem;
    background-color: transparent;
    border: 2px solid #b79240;
    color: #b79240;
    font-weight: 600;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease;
    text-align: center;
    user-select: none;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    font-size: 1.1rem;
    min-width: 0;
}

.purchaseBtn {
    flex: 1 1 50%;
    padding: 0.6rem 1rem;
    background-color: #00753C;
    color: white;
    font-weight: 700;
    border: 2px solid #00753C;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease, border-color 0.3s ease;
    text-align: center;
    user-select: none;
    font-size: 1.2rem;
    min-width: 0;
}

.purchaseNote {
  margin-top: 3rem;
  padding: 1rem 1.2rem;
  background-color: #e6f0e8; /* ì•„ì£¼ ì—°í•œ ì—°ë‘ë¹› ë°°ê²½ */
  border-left: 4px solid #003016; /* ì§„í•œ ë…¹ìƒ‰ ê°•ì¡°ì„  */
  border-radius: 6px;
  color: #003016; /* í…ìŠ¤íŠ¸ë„ ì§„í•œ ë…¹ìƒ‰ */
  font-size: 0.95rem;
  line-height: 1.5;
  box-shadow: 0 2px 5px rgba(0, 48, 22, 0.15); /* ì—°í•œ ë…¹ìƒ‰ ê·¸ë¦¼ìž */
  user-select: text;
  font-family: 'Pretendard', 'Noto Sans KR', sans-serif;
}

.purchaseNote p {
  margin: 0.4rem 0;
}



@media (max-width: 480px) {
    .buttonGroup {
        flex-direction: column;
    }
    .wishlistBtn,
    .purchaseBtn {
        flex: none;
        width: 100%;
    }
}
