.customBackground {
  background-color: #FCFBF2;
  min-height: 100vh; /* í™”ë©´ ì „ì²´ ë†’ì´ í™•ë³´ */
  padding: 2rem 0; /* ìœ„ì•„ëž˜ ì—¬ë°± */
}
/* ê¸°ë³¸ ì»¨í…Œì´ë„ˆ */
.container {
    max-width: 480px;
    margin: 3rem auto;
    padding: 2rem;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
    border: 1px solid #e0e5e2;
}

/* ì œëª© */
.title {
    font-size: 1.8rem;
    font-weight: 600;
    color: #004225;
    margin-bottom: 2rem;
    text-align: center;
}

/* í¼ */
.form {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}

/* í¼ ê·¸ë£¹ */
.formGroup {
    display: flex;
    flex-direction: column;
}

/* ë¼ë²¨ */
.inputLabel {
    margin-bottom: 0.5rem;
    font-size: 0.95rem;
    color: #333;
    font-weight: 500;
}

/* ìž…ë ¥ í•„ë“œ */
.input {
    padding: 0.75rem;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    transition: border-color 0.2s;
}

.input:focus {
    outline: none;
    border-color: #00753C;
}

/* ì—ëŸ¬ í‘œì‹œ */
.inputError {
    border-color: #d9534f;
}

.errorText {
    color: #d9534f;
    font-size: 0.85rem;
    margin-top: 0.3rem;
}

/* ì „í™”ë²ˆí˜¸ ìž…ë ¥ */
.phoneRow {
    display: flex;
    align-items: center;
}

.phoneInput {
    flex: 1;
    padding: 0.7rem;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    text-align: center;
}

.firstPhoneInput {
    flex: 0 0 80px;
}

.dash {
    margin: 0 0.5rem;
    font-size: 1.2rem;
    color: #888;
}

/* ì œì¶œ ë²„íŠ¼ */
.button {
    padding: 0.9rem;
    background-color: #00753C;
    color: #fff;
    font-size: 1rem;
    font-weight: 600;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.2s;
}

.button:hover {
    background-color: #005d2e;
}

/* ë°˜ì‘í˜• */
@media (max-width: 500px) {
    .container {
        padding: 1.5rem;
    }

    .title {
        font-size: 1.5rem;
    }

    .input,
    .phoneInput {
        font-size: 0.95rem;
    }

    .button {
        font-size: 0.95rem;
    }
}
