.bookHeader {
  display: flex;
  align-items: flex-start;
  gap: 1.5rem;
  border-bottom: 1px solid #d0e3ce;
  padding-bottom: 1.5rem;
  margin-bottom: 1.5rem;
}

.bookImage {
  width: 160px;
  height: auto;
  border-radius: 6px;
  box-shadow: 0 2px 6px rgba(0, 48, 22, 0.15);
  object-fit: cover;
  background-color: #e8f0e8;
}

.bookInfo {
